.intro {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 2em;
  background: rgba(255, 255, 255, .7);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;

  /* display: none */
}
.intro.hide {
  -webkit-transition: opacity 500ms 0ms, height 0ms 510ms;
  -o-transition: opacity 500ms 0ms, height 0ms 510ms;
  transition: opacity 500ms 0ms, height 0ms 510ms;
  opacity: 0;
  height: 0;
}

.side_section{
  position: fixed;
  height: 100vh;
  width: 400px;
  top: 0;
  left: -120%;
  padding: 1em;
  background: white;
  z-index: 2;
  -webkit-transition: left 500ms;
  -o-transition: left 500ms;
  transition: left 500ms;

}
.side_section img{
  width: 100%
}
.side_section.show{
  left: 0;
}

.main_section{
  position: fixed;
  overflow: auto;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: -120%;
  padding: 1em;
  background: white;
  z-index: 2;
  opacity: 0;
  -webkit-transition: opacity 500ms, left 0ms 510ms;
  -o-transition: opacity 500ms, left 0ms 510ms;
  transition: opacity 500ms, left 0ms 510ms;
}
.main_section.show{
  opacity: 1;
  left: 0;
  -webkit-transition: opacity 500ms 10ms, left 0ms 0ms;
  -o-transition: opacity 500ms 10ms, left 0ms 0ms;
  transition: opacity 500ms 10ms, left 0ms 0ms;
}



.logo {
  width: 100%;
  max-width: 400px;
}
.description {
  line-height: 1em;
  width: 500px;
}

.description p {
  margin: 0.5em 0;
}




